@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
/* Style guide for CSS
  -class names lowercase with dashes (kebab case), e.g. "user-input"
  -colors in hexadecimal, e.g #fefefe
  -rules in separate lines
  -follow Object Oriented CSS if necessary https://github.com/stubbornella/oocss/wiki
*/

body {
  font-family: 'Share Tech Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* this makes the body a flex container
  and the App component within centered*/
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  background-color: black;
  color: limegreen;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ol, ul{
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

li, p {
  line-height: 1.25;
  margin:0px;
}

.game-board{
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1ch;
  gap: 1ch;
  max-width: -webkit-max-content;
  max-width: max-content;
}

.remaining-attempts-text{
  grid-column: 1;
  grid-row: 1;
}

.remaining-attempts{
  grid-column: 2;
  grid-row: 1;
}

.column-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 1ch;
  gap: 1ch;

  margin:0px;
  padding: 0px;
  max-width: -webkit-max-content;
  max-width: max-content;
}

.first-column-container{
  grid-column:1;
  grid-row:2;

}

.second-column-container{
 grid-column:2;
 grid-row:2; 
}

.feedback-column{
  grid-column:3;
  grid-row:2;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.feedback-grid{
  display: grid
}

.previous-selection{
  grid-row: 1;
}

.current-selection{
  grid-row: 2;
}

.highlighted-symbol{
  color: black;
  background-color: limegreen;
}

.attempt-bullet{
  height: 100%;
  background-color: limegreen;
  margin-right: 1ch;
}

.welcome-message{
  margin-bottom: 1%;
}
.App {
  text-align: left;
  font-size: 2vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

